import { observable, action, runInAction } from "mobx";
import BaseStore from "@/pages/store";
import DataApiService, { getUserInfo } from "@/service/http";
// import { getWeChatCache } from '@/service/wechatAxiosInstance';
import { fetchMeetingNotificationDetail } from "@/service/http/meeting/notification";
import { IMeetingItem, ISignError } from "./interface";
import { EnumState, KeyOfIState } from "./constance";
import axios from "axios";
import { Toast } from "antd-mobile";

interface IMedia {
  getCamera: any;
  getLocalImgData: any;
  uploadImage: any;
}

export class MeetingListStore extends BaseStore {
  constructor(props: any) {
    super(props);
    this.haveSignInList = props.haveSignInList;
    this.notSignInList = props.notSignInList;
    this.willSignInList = props.willSignInList;
    this.easyNarBar = props.easyNarBar;
    this.history = props.history;
    this.MeetingList = props.MeetingList;
    this.tokenContext = props.tokenContext;
  }
  @observable haveSignInList: IMeetingItem[] | void;
  @observable notSignInList: IMeetingItem[] | void;
  @observable willSignInList: IMeetingItem[] | void;
  @observable easyNarBar: TReactNode;
  @observable willSignInListState: KeyOfIState = EnumState["noop"];
  @observable workNumber: string = "";
  history: any;
  MeetingList: TReactNode;
  tokenContext: IToken;

  @action
  fetchWillSignInList(skip: number, limit: number, params: any) {
    return new Promise((resolve, reject) => {
      DataApiService.getShowTableData({
        tableName: "meeting_info_wx_view",
        length: limit,
        start: skip,
        type: "TABLE_QUERY",
        subQuery: [],
        fieldName: [
          "meeting_info_wx_view.id",
          "meeting_info_wx_view.startTime",
          "meeting_info_wx_view.endTime",
          "meeting_info_wx_view.name",
          "meeting_info_wx_view.address",
          "meeting_info_wx_view.roomName",
          "meeting_info_wx_view.type"
        ],
        controlPermissionModelDTOs: [],
        params
      })
        .then(data => {
          const { result, success } = data;
          if (success) {
            resolve(result);
          } else {
            reject(new Error("数据错误，请确认"));
          }
        })
        .catch(err => {
          reject(err);
        });
    });
  }
}
export class MeetingDetailStore extends BaseStore {
  constructor(props: any) {
    super(props);
    this.history = props.history;
    this.easyNarBar = props.easyNarBar;
    this.tokenContext = props.tokenContext;
  }
  @observable signError: ISignError = { message: null };
  @observable memberName: string = "";
  @observable signIning: boolean = false;
  history: any;
  easyNarBar: TReactNode;
  tokenContext: IToken;

  @action reset = () => {
    this.signError = { message: null };
    this.memberName = "";
  };

  @action fetchDetail = (id: string) => {
    return new Promise((resolve, reject) => {
      fetchMeetingNotificationDetail({ id })
        .then((data: any) => {
          const { result, success } = data;
          if (success) {
            let detail = result.dataJson.meeting_details_info_wx_view[0];
            resolve(detail);
          } else {
            reject(new Error("数据错误，请确认"));
          }
        })
        .catch(err => {
          reject(err);
        });
    });
  };

  @action getMemberName = () => {
    getUserInfo().then((res: any) => {
      if (res && res.code === 0) {
        const result = res.result;
        const baseUserInfo = JSON.parse(result.baseUserInfo) || "";
        if (baseUserInfo) {
          this.memberName = baseUserInfo.memberName;
        }
      }
    });
  };

  @action.bound
  signIn(media: IMedia, detail: any, hasToBase64: boolean, cb?: Function) {
    const { id, type } = detail;
    if (this.signIning) return;
    Toast.loading("签到中，请稍等", 0);
    this.signIning = true;
    this.callGetCamera(media.getCamera.getCamera)
      .then(localId => {
        if (hasToBase64) {
          this.callGetLocalImgData(media.getLocalImgData.getLocalImgData, localId)
            .then(localData => {
              this.checkAndSignIn(localData, id, type, cb);
            })
            .catch(() => {
              runInAction(() => {
                this.signError = {
                  message: "微信获取照片数据异常"
                };
                Toast.hide();
                setTimeout(() => {
                  Toast.fail(this.signError.message, 1);
                }, 50);
              });
            });
        } else {
          const uploadImage = media.uploadImage.uploadImage;
          uploadImage({
            localId: localId,
            isShowProgressTips: 0,
            success: (res: any) => {
              this.toDataURL(res.serverId, (res: string) => {
                this.checkAndSignIn(res, id, type, cb);
              });
            }
          });
        }
      })
      .catch(res => {
        runInAction(() => {
          this.signError = { message: "微信获取照片异常" };
          Toast.hide();
          setTimeout(() => {
            Toast.fail(res.message || this.signError.message, 1);
          }, 50);
        });
      })
      .finally(() => {
        this.signIning = false;
      });
  }

  callGetCamera = (getCamera: Function): Promise<any> => {
    return new Promise((resolve, reject) => {
      getCamera({
        count: 1,
        sizeType: ["compressed"],
        sourceType: ["camera"],
        success: function (res: any) {
          if (res.localIds && res.localIds[0]) {
            resolve(res.localIds[0]);
          } else {
            reject(false);
          }
        },
        fail: function (err: any) {
          console.log("getCamera err", err);
          reject(err);
        },
        cancel: function () {
          reject(new Error("取消签到"));
        }
      });
    });
  };

  callGetLocalImgData = (getLocalImgData: Function, localId: string): Promise<any> => {
    return new Promise((resolve, reject) => {
      getLocalImgData({
        localId,
        success: function (res: any) {
          var localData = res.localData;
          if (localData.indexOf("data:image") != 0) {
            localData = "data:image/jpeg;base64," + localData;
          }
          localData = localData.replace(/\r|\n/g, "").replace("data:image/jgp", "data:image/jpeg");
          localData = localData.replace(/^data:image\/\w+;base64,/, "");
          resolve(localData);
        },
        fail: function (err: any) {
          console.log("getLocalImgData err", err);
          reject(err);
        }
      });
    });
  };

  checkAndSignIn = (localData: string, id: string, type: string, cb?: Function) => {
    this.checkSignIn(localData)
      .then(() => {
        console.log("校验成功");
        this.submit(id, type)
          .then(() => {
            console.log("签到成功");
            Toast.hide();
            Toast.success("签到成功", 1);
            cb && cb();
          })
          .catch(() => {
            console.log("签到失败");
            runInAction(() => {
              this.signError = {
                message: "签到失败"
              };
              Toast.hide();
              setTimeout(() => {
                Toast.fail(this.signError.message, 1);
              }, 50);
            });
          });
      })
      .catch(() => {
        console.log("校验失败");
        runInAction(() => {
          this.signError = {
            message: "校验出错"
          };
          Toast.hide();
          setTimeout(() => {
            Toast.fail(this.signError.message, 1);
          }, 50);
        });
      });
  };

  checkSignIn(picture: string): Promise<any> {
    return new Promise((resolve, reject) => {
      DataApiService.meetingSignInCheck({ picture })
        .then((data: any) => {
          const { code, result } = data;
          if (code === 0 && result) {
            resolve(true);
          } else {
            reject(false);
          }
        })
        .catch(() => {
          reject(false);
        });
    });
  }

  submit(id: string, type: string): Promise<any> {
    return new Promise((resolve, reject) => {
      DataApiService.meetingSignIn({ id, type })
        .then((data: any) => {
          const { success } = data;
          if (success) {
            resolve(true);
          } else {
            reject(false);
          }
        })
        .catch(() => {
          reject(false);
        });
    });
  }

  @action toDataURL = (media_id: string, callback?: Function) => {
    axios
      .request({
        url: `${window.baseURL || ""}/doWeChat/weChat/cgi/getMaterial/${media_id}`,
        method: "GET",
        responseType: "blob"
      })
      .then(res => {
        var reader = new FileReader();
        reader.onloadend = function () {
          let localData = reader.result.replace(/^data:application\/\w+;base64,/, "");
          callback && callback(localData);
        };
        reader.readAsDataURL(res.data);
      });
  };
}
